@import '../../node_modules/include-media/dist/include-media';
@import '../navigation.scss';

/**
 * Responsive breakpoints used by include-media vendor library.
 */
$breakpoints: (
    phone: 23.13em,   // 370px
    phone-lg: 30em,   // 480px
    tablet: 48em,     // 768px
    laptop: 64em,     // 1024px
    laptop-lg: 80em,  // 1280px
    desktop: 90em,    // 1440px
    tv: 120em         // 1920px
);

html {
    background: #eee;
}

img {
    max-width: 100%;
}

.navigation {
    @include navigation();
}
